<template>
  <div class="row" v-if="isReady">
    <div class="col-12 mt-3 flex-x-between">
      <div class="flex-align-center">
        <h3 class="my-0">Products</h3>
      </div>

      <div class="flex-align-center flex-end">
        <el-button
          class="mr-3 my-0 mr-sm-2 sm-hide"
          @click="$router.push('/product-report')"
          type="primary"
          v-ripple.400
        >
          Report
        </el-button>
        <el-button
          class="mx-0 my-0"
          @click="$router.push('/add-new-product')"
          type="primary"
          v-ripple.400
        >
          Add product
        </el-button>
      </div>
    </div>
    <div class="col-12 list-table mt-lg-3 mt-md-3 p-0 pt-sm-0 mt-sm-3">
      <div class="box p-sm-2 pt-sm-2 mb-sm-3">
        <div class="row">
          <!-- <div class="col-12"><div class="b-divider mb-1 mt-0"></div></div> -->
          <div
            class="col-12 mb-1 mb-sm-0 d-flex sm-hide justify-content-between align-items-center"
          >
            <div class="flex-align-center sm-hide">
              <!-- <el-select
                v-model="filters.pageSize"
                class="mr-2 page-size-selector mr-2"
              >
                <el-option
                  v-for="page in 100"
                  :key="page"
                  :label="page * 15"
                  :value="page * 15"
                >
                </el-option>
              </el-select> -->
              <el-input
                v-model="inputHelper"
                @change="setFilterQuery"
                class="table-search my-sm-0"
                placeholder="Type something"
                prefix-icon="bx bx-search"
              >
              </el-input>
            </div>
            <el-popover placement="bottom" width="200" trigger="click">
              <p class="font-weight-bold">Sort By</p>
              <div class="b-divider my-2"></div>
              <div class="w-100">
                <el-radio
                  v-model="filters.order_by"
                  v-for="(sort, n) in sortList"
                  :key="sort.name"
                  :class="n !== sortList.length - 1 && 'mb-3'"
                  :label="sort.value"
                >
                  {{ sort.name }}
                </el-radio>
              </div>
              <el-button
                v-ripple.400="'rgba(0,0,0,0.1)'"
                class="flat no-width"
                slot="reference"
              >
                <i class="bx bx-sort mr-1"></i> Sort
              </el-button>
            </el-popover>
          </div>

          <div class="col-12 w-100 lg-hide md-hide d-flex align-items-center">
            <el-input
              v-model="inputHelper"
              @change="setFilterQuery"
              class="w-100 mr-2 my-sm-0"
              placeholder="Type something"
              prefix-icon="bx bx-search"
            >
            </el-input>
            <el-popover placement="bottom" width="200" trigger="click">
              <p class="font-weight-bold">Sort By</p>
              <div class="b-divider my-2"></div>
              <div class="w-100">
                <el-radio
                  v-model="filters.order_by"
                  v-for="(sort, n) in sortList"
                  :key="sort.name"
                  :class="n !== sortList.length - 1 && 'mb-3'"
                  :label="sort.value"
                >
                  {{ sort.name }}
                </el-radio>
              </div>
              <el-button
                v-ripple.400="'rgba(0,0,0,0.1)'"
                class="flat no-width my-sm-0 lg-hide md-hide"
                slot="reference"
              >
                <i class="bx bx-sort mr-1"></i> Sort
              </el-button>
            </el-popover>
          </div>
        </div>

        <div class="w-100 sm-hide el-main-table">
          <div class="el-table-header" v-if="selected.length > 0">
            <div class="d-flex align-items-center">
              <el-checkbox
                :indeterminate="selected.length == products.results.length"
                v-model="allCheck"
                @change="$refs.multipleTable.toggleAllSelection"
                >{{ selected.length }} selected</el-checkbox
              >
              <el-dropdown @command="updateStatus" trigger="click">
                <p
                  class="el-dropdown-link ml-3 pointer primary hover-underline"
                >
                  Actions
                  <i class="el-icon-arrow-down el-icon--right"></i>
                </p>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item
                    v-for="action in actions"
                    :key="action"
                    :command="action"
                  >
                    {{ action }}</el-dropdown-item
                  >
                </el-dropdown-menu>
              </el-dropdown>
            </div>
          </div>
          <el-table
            ref="multipleTable"
            :data="products.results"
            style="width: 100%"
            @row-click="
              (row) =>
                $router.push({
                  name: 'Edit product',
                  params: { id: row.id },
                })
            "
            @selection-change="handleSelectionChange"
          >
            <el-table-column type="selection"> </el-table-column>
            <el-table-column label="Product" width="120">
              <template slot-scope="scope">
                <img
                  :src="scope.row.image"
                  style="
                    height: 80px;
                    width: 80px;
                    object-fit: cover;
                    border-radius: 5px;
                    margin-right: 5px;
                  "
                  alt=""
                />
              </template>
            </el-table-column>
            <el-table-column label="Name" width="300">
              <template slot-scope="scope">
                <span>{{ scope.row.name }}</span>
              </template>
            </el-table-column>

            <el-table-column label="Price" width="150">
              <template slot-scope="scope">
                {{ scope.row.price + "MMK" }}
              </template>
            </el-table-column>
            <el-table-column label="Barcode">
              <template slot-scope="scope">
                <span>{{ scope.row.barcode }}</span>
              </template>
            </el-table-column>
            <el-table-column label="Stocks">
              <template slot-scope="scope">
                <span>{{ scope.row.number_of_stock }}</span>
              </template>
            </el-table-column>
            <template #empty>
              <el-empty description="No data found!">
                <template #image> <empty /> </template
              ></el-empty>
            </template>
          </el-table>
          <div class="el-table-footer" v-loading="loading">
            <p class="pt-3"></p>
            <!-- <el-pagination
              background
              @current-change="
                (index) => ((filters.page = index), getproducts())
              "
              :current-page="parseInt(filters.page)"
              layout="prev, pager, next"
              :page-count="products.total_pages"
            >
            </el-pagination> -->
          </div>
        </div>
      </div>

      <div class="row px-2 lg-hide md-hide">
        <div
          v-for="product in products.results"
          :key="product.id"
          class="col-6 px-1 mb-sm-3"
          @click="
            () =>
              $router.push({
                name: 'Edit product',
                params: { id: product.id },
              })
          "
        >
          <div class="sale-item p-2 box">
            <img
              @click="openLightBox(product.image)"
              :src="product.image"
              @load="setHeight"
              style="
                width: 100% !important;
                object-fit: cover;
                border-radius: 5px;
              "
              alt=""
            />

            <div class="mt-2 sm-text mb-2">
              <p class="mb-2">Code : {{ product.barcode }}</p>
              <p class="mb-2">Price : {{ product.price + "MMK" }}</p>
              <p class="mb-2">No. Of Stocks : {{ product.number_of_stock }}</p>
            </div>
          </div>
        </div>
      </div>

      <div class="lg-hide box md-hide mb-3" v-loading="loading">
        <p class="py-3"></p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { empty } from "../../common";
export default {
  data() {
    return {
      inputHelper: "",
      filters: {
        query: "",
        order_by: "-number_of_stock",
        pageSize: 200,
        page: 1,
      },
      loading: false,
      actions: ["Delete"],
      sortList: [
        {
          name: "Created (oldest first)",
          value: "created_at",
        },
        {
          name: "Number of stocks (Lowest first)",
          value: "number_of_stock",
        },
        {
          name: "Number of stocks (Greatest First)",
          value: "-number_of_stock",
        },
        {
          name: "Created (newest first)",
          value: "-created_at",
        },
        {
          name: "Updated (oldest first)",
          value: "updated_at",
        },
        {
          name: "Updated (newest first)",
          value: "-updated_at",
        },
        {
          name: "Customer A–Z",
          value: "customer__name",
        },
        {
          name: "Customer Z–A",
          value: "-customer__name",
        },
      ],
      loadingDelivey: false,
      deliveries: [],
      selectedDate: null,
      selectedDelivery: null,
      allCheck: false,
      gettingOrder: false,
      page: 1,
      max: 5,
      selected: [],
      isReadyToGet: false,
      products: {
        next: 1,
        results: [],
        total_pages: 0,
      },
      scroll: 0,
    };
  },
  components: { empty },
  computed: {
    ...mapState(["isReady", "store"]),
  },
  created() {
    this.$store.commit("setIsReady", false);
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  mounted() {
    this.filters = {
      query: this.$route.query.query || "",
      order_by: this.$route.query.order_by || "-number_of_stock",
      pageSize: this.$route.query.page_size || "200",
      page: this.$route.query.page || "1",
    };
    (this.inputHelper = this.$route.query.query || ""),
      (this.isReadyToGet = true);
    this.getproducts();
  },
  watch: {
    filters: {
      handler: function (value) {
        if (this.isReadyToGet) {
          this.$router
            .push({
              path: "/products",
              query: {
                query: value.query,
                page_size: value.pageSize,
                page: value.page,
                order_by: value.order_by,
              },
            })
            .catch(() => {});
        }
      },
      deep: true,
    },
  },
  methods: {
    handleScroll(e) {
      const scrollHeight =
        e.target.scrollingElement.scrollHeight -
        e.target.scrollingElement.clientHeight * 2;
      if (scrollHeight < e.target.scrollingElement.scrollTop) {
        if (!this.loading) {
          this.getproducts();
        }
      }
    },
    updateStatus(com) {
      if (com == "Delete") {
        this.deleteProducts();
      }
    },
    deleteProducts() {
      if (window.confirm("Are you sure, you want to delete this product?")) {
        this.loading = true;
        var data = [];
        this.selected.forEach((ele) => {
          data.push(ele.id);
        });
        var url = `products/delete/`;
        this.$axios
          .post(url, { data: data })
          .then(() => {
            this.$noti.display({
              message: "Success!",
              status: "success",
              color: "success",
            });
            window.location.reload();
          })
          .catch((err) => {
            this.loading = false;
            this.$noti.display({
              message: err,
              status: "error",
              color: "error",
            });
          });
      }
    },
    setFilterQuery() {
      this.filters.query = this.inputHelper;
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.selected = val;
      console.log(
        "🚀 ~ file: list.vue:410 ~ handleSelectionChange ~ this.selected:",
        this.selected
      );
    },
    setHeight(e) {
      e.path[0].style.height = e.path[0].clientWidth + "px";
    },
    getproducts() {
      if (this.products.next) {
        this.loading = true;
        this.gettingOrder = true;
        var url = `products/?page=${this.products.next}&page_size=${this.filters.pageSize}&query=${this.filters.query}&order_by=${this.filters.order_by}`;
        this.$axios
          .get(url)
          .then((response) => {
            this.gettingOrder = false;
            const scroll = window.scrollY;
            this.products = {
              ...response.data,
              results: [...this.products.results, ...response.data.results],
            };
            setTimeout(() => {
              window.scroll(0, scroll);
            }, 50);
            setTimeout(() => {
              this.loading = false;
            }, 100);

            this.$store.commit("setIsReady", true);
          })
          .catch((err) => {
            this.loading = false;
            this.gettingOrder = false;
            this.$noti.display({
              message: err,
              status: "error",
              color: "error",
            });
          });
      }
    },
  },
};
</script>

